.closeWindow {
  position: absolute;
  top: 12px;
  right: 12px;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  background: transparent;
  border: transparent;
  cursor: pointer;
}
.closeWindow::before,
.closeWindow::after {
  content: "";
  display: block;
  width: 25px;
  height: 4px;
  background: black;
  position: absolute;
  top: 13px;
  left: 5px;
}
.closeWindow::before {
  transform: rotate(45deg);
}

.closeWindow::after {
  transform: rotate(-45deg);
}

.closeWindow span {
  font-weight: bold;
  font-size: 30px;
  line-height: 0;
}
