.container {
  width: 100%;
  padding-top: 140px;
}

.input {
  display: block;
  height: 50px;
  width: 100%;
  font-size: 20px;
  border-radius: 10px;
  border-width: 2px;
  padding: 5px 10px;
  margin: 0px 0px 15px 0px;
  background: #ccc;
}

.input:focus {
  outline: 2px solid black;
}

.button {
  margin: 15px 0px 0px 0px;
  height: 40px;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background: #9e9e9e;
  font-size: 20px;
  padding: 10px 0px;
  cursor: pointer;
}
.button:active {
  background: #6b6b6b;
  color: #9e9e9e;
}
.mainContainer {
  font-size: 40px;
  text-align: center;
  margin: 0px 0px 40px 0px;
  text-shadow: 1px 1px 1px black;
}
.onlyDays {
  display: flex;
  align-items: center;
  cursor: pointer;
  width: max-content;
}
.onlyDays input {
  width: 20px;
  height: 20px;
  margin-right: 10px;
  cursor: pointer;
}
.onlyDays span {
  font-size: 22px;
  position: relative;
  top: -1.5px;
}
