.formulsContainer {
  width: 100%;
}

.formulsList {
  background: #ccc;
  width: 100%;
  padding: 30px;
  position: absolute;
  bottom: 0px;
  left: 0px;
  border-radius: 20px;
  border: 1px solid black;
}

.formulsList li {
  margin: 0px 0px 10px 0px;
}

.openFormuls {
  width: 190px;
  padding: 10px 5px;
  border-radius: 10px;
  background: #9e9e9e;
  position: absolute;
  bottom: 20px;
  cursor: pointer;
}

.openFormuls:active {
  background: #6b6b6b;
  color: #9e9e9e;
}
