.container {
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 265px 30px 10px;
}

.modal {
  width: 100%;
  height: 115px;
  background: #ccc;
  border-radius: 10px;
  padding: 10px;
}
.confirmButton {
  display: block;
  padding: 10px;
  border-radius: 10px;
  width: 100px;
  margin: 0 auto;
  cursor: pointer;
}
.modalText {
  text-align: center;
  margin-bottom: 25px;
  font-size: 20px;
}
